/**
 * APIÈÖçÁΩÆ‰ø°ÊÅØ
 */

// Êï∞ÊçÆÊ∫êÁ±ªÂûãÊûö‰∏æ
export const DataSourceType = {
  LOCAL: 'local', // ‰ªépublicÊñá‰ª∂Â§πËé∑Âèñ
  REMOTE: 'remote' // ‰ªéËøúÁ®ãAPIËé∑Âèñ
};

// Âä®ÊÄÅÊï∞ÊçÆÈõÜÈÖçÁΩÆÁºìÂ≠ò
let datasetsConfig = null;
let configLoadPromise = null;
let isConfigLoading = false;

/**
 * Âä†ËΩΩÊï∞ÊçÆÈõÜÈÖçÁΩÆ
 * @returns {Promise<Object>} Êï∞ÊçÆÈõÜÈÖçÁΩÆÂØπË±°
 */
export async function loadDatasetsConfig() {
  // Â¶ÇÊûúÂ∑≤ÁªèÊúâÂä†ËΩΩ‰∏≠ÁöÑPromiseÔºåÁõ¥Êé•ËøîÂõû
  if (configLoadPromise) {
    console.log('ÈÖçÁΩÆÊ≠£Âú®Âä†ËΩΩ‰∏≠ÔºåËøîÂõûÁé∞ÊúâPromise');
    return configLoadPromise;
  }
  
  // Â¶ÇÊûúÈÖçÁΩÆÂ∑≤ÁºìÂ≠òÔºåÁõ¥Êé•ËøîÂõû
  if (datasetsConfig) {
    console.log('‰ΩøÁî®ÁºìÂ≠òÁöÑÈÖçÁΩÆ');
    return Promise.resolve(datasetsConfig);
  }
  
  // Èò≤Ê≠¢Âπ∂ÂèëÂä†ËΩΩ
  if (isConfigLoading) {
    console.log('ÈÖçÁΩÆÂä†ËΩΩ‰∏≠ÔºåÁ≠âÂæÖÂÆåÊàê...');
    // Á≠âÂæÖÂä†ËΩΩÂÆåÊàê
    while (isConfigLoading && !datasetsConfig) {
      await new Promise(resolve => setTimeout(resolve, 100));
    }
    return datasetsConfig || Promise.reject(new Error('ÈÖçÁΩÆÂä†ËΩΩÂ§±Ë¥•'));
  }
  
  // ËÆæÁΩÆÂä†ËΩΩÁä∂ÊÄÅ
  isConfigLoading = true;
  
  // ÂàõÂª∫Âä†ËΩΩPromise
  configLoadPromise = (async () => {
    try {
      const response = await fetch('/Data/datasets_config.json');
      if (!response.ok) {
        throw new Error(`Failed to load datasets config: ${response.status}`);
      }
      
      const config = await response.json();
      console.log('config', config,defaultDatasets,'399999999999999');
      // È™åËØÅÈÖçÁΩÆÊ†ºÂºè
      if (!config.datasets || !Array.isArray(config.datasets)) {
        throw new Error('Invalid datasets config format: missing datasets array');
      }
      
      // ËøáÊª§ÂêØÁî®ÁöÑÊï∞ÊçÆÈõÜ
      config.datasets = config.datasets.filter(dataset => dataset.enabled !== false);
      
      // È™åËØÅÈªòËÆ§Êï∞ÊçÆÈõÜÂ≠òÂú®
      if(defaultDatasets){
        config.defaultDataset = defaultDatasets;
      }
      else if (config.defaultDataset && !config.datasets.find(d => d.id === config.defaultDataset)) {
        console.warn(`ÈÖçÁΩÆÁöÑÈªòËÆ§Êï∞ÊçÆÈõÜ '${config.defaultDataset}' Âú®ÂêØÁî®ÁöÑÊï∞ÊçÆÈõÜ‰∏≠‰∏çÂ≠òÂú®`);
        config.defaultDataset = config.datasets.length > 0 ? config.datasets[0].id : null;
        if (config.defaultDataset) {
          console.info(`Â∑≤Ëá™Âä®ËÆæÁΩÆÈªòËÆ§Êï∞ÊçÆÈõÜ‰∏∫: ${config.defaultDataset}`);
        }
      } else if (!config.defaultDataset && config.datasets.length > 0) {
        // Â¶ÇÊûúÈÖçÁΩÆÊñá‰ª∂‰∏≠Ê≤°ÊúâÊåáÂÆödefaultDatasetÔºåËá™Âä®ËÆæÁΩÆ‰∏∫Á¨¨‰∏Ä‰∏™
        config.defaultDataset = config.datasets[0].id;
        console.info(`ÈÖçÁΩÆÊñá‰ª∂‰∏≠Êú™ÊåáÂÆödefaultDatasetÔºåËá™Âä®ËÆæÁΩÆ‰∏∫Á¨¨‰∏Ä‰∏™Êï∞ÊçÆÈõÜ: ${config.defaultDataset}`);
      }
      
      datasetsConfig = config;
      console.log('Êï∞ÊçÆÈõÜÈÖçÁΩÆÂä†ËΩΩÊàêÂäü:', config);
      return config;
      
    } catch (error) {
      console.error('Âä†ËΩΩÊï∞ÊçÆÈõÜÈÖçÁΩÆÂ§±Ë¥•:', error);
      
             // ËøîÂõûÈªòËÆ§ÈÖçÁΩÆ‰Ωú‰∏∫ÂõûÈÄÄ - ‰ΩøÁî®ÂÆûÈôÖÂ≠òÂú®ÁöÑÊï∞ÊçÆÈõÜ
       const fallbackConfig = {
         datasets: [
           {
             id: 'Tumour',
             name: 'Tumour',
             displayName: 'Tumour scRNAseq',
             description: 'Main single cell RNA-seq dataset',
             basePath: '/Tumour',
             enabled: true,
             defaultVisualization: 'umap',
             supportedVisualizations: ['tsne', 'umap', 'pca']
           },
           {
             id: 'mouse',
             name: 'mouse',
             displayName: 'Mouse scRNAseq', 
             description: 'Alternative single cell RNA-seq dataset',
             basePath: '/mouse',
             enabled: true,
             defaultVisualization: 'umap',
             supportedVisualizations: ['tsne', 'umap', 'pca']
           }
         ],
         defaultDataset: 'Tumour',
         config: {
           loadTimeout: 30000,
           cacheTTL: 300000,
           retryAttempts: 3,
           autoDetectCellCount: true
         }
       };
       
       console.warn('‰ΩøÁî®ÂõûÈÄÄÈÖçÁΩÆÔºåÈªòËÆ§Êï∞ÊçÆÈõÜËÆæÁΩÆ‰∏∫:', fallbackConfig.defaultDataset);
      
      datasetsConfig = fallbackConfig;
      return fallbackConfig;
    } finally {
      isConfigLoading = false;
      configLoadPromise = null;
    }
  })();
  
  return configLoadPromise;
}

/**
 * Ëé∑ÂèñÊï∞ÊçÆÈõÜÈÖçÁΩÆÔºàÂêåÊ≠•ÊñπÊ≥ïÔºåÈúÄË¶ÅÂÖàË∞ÉÁî®loadDatasetsConfigÔºâ
 * @returns {Object|null} Êï∞ÊçÆÈõÜÈÖçÁΩÆÂØπË±°
 */
export function getDatasetsConfig() {
  return datasetsConfig;
}

/**
 * Ê†πÊçÆÊï∞ÊçÆÈõÜÈÖçÁΩÆÂä®ÊÄÅÁîüÊàêDatasetTypeÊûö‰∏æ
 * @returns {Object} Êï∞ÊçÆÈõÜÁ±ªÂûãÊûö‰∏æÂØπË±°
 */
export function getDatasetTypes() {
  if (!datasetsConfig) {
    // Â¶ÇÊûúÈÖçÁΩÆÊú™Âä†ËΩΩÔºåËøîÂõûÈªòËÆ§Êûö‰∏æ
    return {
      SINGLE_OUTPUTS: 'single_outputs',
      SINGLE_OUTPUTS1: 'single_outputs1'
    };
  }
  
  const types = {};
  datasetsConfig.datasets.forEach(dataset => {
    const enumKey = dataset.id.toUpperCase();
    types[enumKey] = dataset.id;
  });
  
  return types;
}

// ‰øùÊåÅÂêëÂêéÂÖºÂÆπÁöÑDatasetType - Â∞ÜÂú®ÈÖçÁΩÆÂä†ËΩΩÂêéÂä®ÊÄÅÊõ¥Êñ∞
export let DatasetType = {
  SINGLE_OUTPUTS: 'single_outputs',
  SINGLE_OUTPUTS1: 'single_outputs1',
  SINGLE_OUTPUTS_QUADTREE_ORDER: 'single_outputs_quadtree_order',
  SINGLE_OUTPUTS_ORIGINAL_ORDER: 'single_outputs_original_order'
};

// ÈªòËÆ§Êï∞ÊçÆÊ∫êÁ±ªÂûã
let currentDataSource = DataSourceType.LOCAL;

// ÈªòËÆ§Êï∞ÊçÆÈõÜ - Â∞Ü‰ªéÈÖçÁΩÆÊñá‰ª∂Âä®ÊÄÅÁ°ÆÂÆö
let currentDataset = null;

// Êï∞ÊçÆÈõÜÂàáÊç¢Áä∂ÊÄÅÊ†áÂøó
let isDatasetSwitching = false;

// APIËØ∑Ê±ÇÂéªÈáçÊú∫Âà∂
const pendingRequests = new Map();

/**
 * ÂàõÂª∫ÂéªÈáçÁöÑAPIËØ∑Ê±Ç
 * @param {string} key - ËØ∑Ê±ÇÂîØ‰∏ÄÊ†áËØÜ
 * @param {Function} requestFn - ËØ∑Ê±ÇÂáΩÊï∞
 * @returns {Promise} ËØ∑Ê±ÇÁªìÊûú
 */
export function createDedupedRequest(key, requestFn) {
  // Â¶ÇÊûúÂ∑≤ÊúâÁõ∏ÂêåËØ∑Ê±ÇÂú®ËøõË°å‰∏≠ÔºåËøîÂõûÁé∞ÊúâPromise
  if (pendingRequests.has(key)) {
    console.log(`APIËØ∑Ê±ÇÂéªÈáç: ${key}`);
    return pendingRequests.get(key);
  }
  
  // ÂàõÂª∫Êñ∞ËØ∑Ê±Ç
  const request = requestFn().finally(() => {
    // ËØ∑Ê±ÇÂÆåÊàêÂêéÊ∏ÖÈô§ÁºìÂ≠ò
    pendingRequests.delete(key);
  });
  
  // ÁºìÂ≠òËØ∑Ê±ÇPromise
  pendingRequests.set(key, request);
  
  return request;
}

/**
 * Ê£ÄÊü•ÊòØÂê¶Ê≠£Âú®ËøõË°åÊï∞ÊçÆÈõÜÂàáÊç¢
 * @returns {boolean} ÊòØÂê¶Ê≠£Âú®ÂàáÊç¢Êï∞ÊçÆÈõÜ
 */
export function getDatasetSwitchingStatus() {
  return isDatasetSwitching;
}

/**
 * ËÆæÁΩÆÊï∞ÊçÆÈõÜÂàáÊç¢Áä∂ÊÄÅ
 * @param {boolean} switching - ÊòØÂê¶Ê≠£Âú®ÂàáÊç¢
 */
export function setDatasetSwitching(switching) {
  isDatasetSwitching = switching;
  console.log(`Êï∞ÊçÆÈõÜÂàáÊç¢Áä∂ÊÄÅ: ${switching ? 'ËøõË°å‰∏≠' : 'ÂÆåÊàê'}`);
}

/**
 * ËÆæÁΩÆÊï∞ÊçÆÊ∫êÁ±ªÂûã
 * @param {string} sourceType - Êï∞ÊçÆÊ∫êÁ±ªÂûãÔºå‰ΩøÁî®DataSourceTypeÊûö‰∏æ
 */
export function setDataSource(sourceType) {
  if (Object.values(DataSourceType).includes(sourceType)) {
    currentDataSource = sourceType;
    console.log(`Êï∞ÊçÆÊ∫êÂ∑≤ÂàáÊç¢‰∏∫: ${sourceType}`);
  } else {
    console.error('Êó†ÊïàÁöÑÊï∞ÊçÆÊ∫êÁ±ªÂûã');
  }
}

/**
 * Ëé∑ÂèñÂΩìÂâçÊï∞ÊçÆÊ∫êÁ±ªÂûã
 * @returns {string} ÂΩìÂâçÊï∞ÊçÆÊ∫êÁ±ªÂûã
 */
export function getDataSource() {
  return currentDataSource;
}

/**
 * È™åËØÅÊï∞ÊçÆÈõÜÁ±ªÂûãÊòØÂê¶ÊúâÊïà
 * @param {string} datasetType - Êï∞ÊçÆÈõÜÁ±ªÂûã
 * @returns {boolean} ÊòØÂê¶ÊúâÊïà
 */
export function isValidDatasetType(datasetType) {
  // Ê£ÄÊü•Âä®ÊÄÅÈÖçÁΩÆ
  if (datasetsConfig && datasetsConfig.datasets) {
    return datasetsConfig.datasets.some(dataset => dataset.id === datasetType);
  }
  
  // ÂõûÈÄÄÂà∞ÈùôÊÄÅDatasetTypeÊ£ÄÊü•
  return Object.values(DatasetType).includes(datasetType);
}

/**
 * Ëé∑ÂèñÊï∞ÊçÆÈõÜ‰ø°ÊÅØ
 * @param {string} datasetType - Êï∞ÊçÆÈõÜÁ±ªÂûã
 * @returns {Object|null} Êï∞ÊçÆÈõÜ‰ø°ÊÅØÂØπË±°
 */
export function getDatasetInfo(datasetType) {
  if (datasetsConfig && datasetsConfig.datasets) {
    return datasetsConfig.datasets.find(dataset => dataset.id === datasetType) || null;
  }
  return null;
}

/**
 * ËÆæÁΩÆÂΩìÂâçÊï∞ÊçÆÈõÜ
 * @param {string} datasetType - Êï∞ÊçÆÈõÜÁ±ªÂûã
 */
export function setDataset(datasetType) {
  if (isValidDatasetType(datasetType)) {
    const oldDataset = currentDataset;
    currentDataset = datasetType;
    console.log(`Êï∞ÊçÆÈõÜÂ∑≤ÂàáÊç¢‰∏∫: ${datasetType}`);
    
    // Â¶ÇÊûúÊï∞ÊçÆÈõÜÂèëÁîü‰∫ÜÂèòÂåñÔºåÊ∏ÖÈô§Áõ∏ÂÖ≥ÁºìÂ≠ò
    if (oldDataset !== datasetType) {
      console.log('Êï∞ÊçÆÈõÜÂàáÊç¢ÔºåÊ∏ÖÈô§Áõ∏ÂÖ≥ÁºìÂ≠ò...');
      
      // üî• ËÆæÁΩÆÂàáÊç¢Áä∂ÊÄÅÔºåÊäëÂà∂ÈîôËØØÊ∂àÊÅØ
      setDatasetSwitching(true);
      
      // Âä®ÊÄÅÂØºÂÖ•Âπ∂Ê∏ÖÈô§ÁºìÂ≠ò
      import('./apiCache').then(({ default: apiCache }) => {
        // Ê∏ÖÈô§Â≠óÊÆµ‰ø°ÊÅØÁºìÂ≠ò
        apiCache.invalidate('fields_info');
        // Ê∏ÖÈô§ÁªÜËÉûÁ±ªÂûãÂÄºÁºìÂ≠ò
        apiCache.invalidate('cell_type_values');
        // Ê∏ÖÈô§Âü∫Âõ†ÂàóË°®ÁºìÂ≠òÔºàÂ¶ÇÊûúÊúâÁöÑËØùÔºâ
        apiCache.invalidate('gene_list');
        
        console.log('ÁºìÂ≠òÊ∏ÖÈô§ÂÆåÊàê');
        
        // Âª∂ËøüÈáçÁΩÆÂàáÊç¢Áä∂ÊÄÅÔºåÁªôÁªÑ‰ª∂Ë∂≥Â§üÊó∂Èó¥ÂÆåÊàêÈáçÊñ∞Âä†ËΩΩ
        setTimeout(() => {
          setDatasetSwitching(false);
        }, 2000);
        
      }).catch(error => {
        console.warn('Ê∏ÖÈô§ÁºìÂ≠òÊó∂Âá∫Èîô:', error);
        setDatasetSwitching(false);
      });
    }
    
    // Ëß¶ÂèëÊï∞ÊçÆÈõÜÂàáÊç¢‰∫ã‰ª∂ÔºåÂåÖÂê´ÊóßÊï∞ÊçÆÈõÜ‰ø°ÊÅØÁî®‰∫éÁºìÂ≠òÊ∏ÖÁêÜ
    if (typeof window !== 'undefined') {
      window.dispatchEvent(new CustomEvent('datasetChanged', { 
        detail: { 
          dataset: datasetType,
          oldDataset: oldDataset  // üî• Ê∑ªÂä†ÊóßÊï∞ÊçÆÈõÜ‰ø°ÊÅØ
        } 
      }));
    }
  } else {
    console.error('Êó†ÊïàÁöÑÊï∞ÊçÆÈõÜÁ±ªÂûã:', datasetType);
  }
}

/**
 * Ëé∑ÂèñÂΩìÂâçÊï∞ÊçÆÈõÜ
 * @returns {string|null} ÂΩìÂâçÊï∞ÊçÆÈõÜÔºåÂ¶ÇÊûúÊó†Ê≥ïÁ°ÆÂÆöÂàôËøîÂõû null
 */
export function getDataset() {
  console.log('currentDataset', currentDataset);
  
  // Â¶ÇÊûúÂΩìÂâçÊï∞ÊçÆÈõÜ‰∏∫Á©∫ÔºåÂ∞ùËØï‰ªéÈÖçÁΩÆËé∑ÂèñÈªòËÆ§Êï∞ÊçÆÈõÜ
  if (!currentDataset && datasetsConfig) {
    // ‰ºòÂÖà‰ΩøÁî®ÈÖçÁΩÆÊñá‰ª∂‰∏≠ÊåáÂÆöÁöÑÈªòËÆ§Êï∞ÊçÆÈõÜ
    currentDataset = datasetsConfig.defaultDataset;
    
    // Â¶ÇÊûúÈÖçÁΩÆ‰∏≠Ê≤°ÊúâÊåáÂÆöÈªòËÆ§Êï∞ÊçÆÈõÜÔºå‰ΩøÁî®Á¨¨‰∏Ä‰∏™ÂèØÁî®ÁöÑÊï∞ÊçÆÈõÜ
    if (!currentDataset && datasetsConfig.datasets && datasetsConfig.datasets.length > 0) {
      currentDataset = datasetsConfig.datasets[0].id;
    }
  }
  
  // ËøîÂõûÂΩìÂâçÊï∞ÊçÆÈõÜÔºåÂ¶ÇÊûúÊó†Ê≥ïÁ°ÆÂÆöÂàôËøîÂõû null
  return currentDataset;
}

/**
 * Ëé∑ÂèñÂΩìÂâçÊï∞ÊçÆÈõÜÔºåÂ¶ÇÊûú‰∏∫Á©∫ÂàôÁ≠âÂæÖÈÖçÁΩÆÂä†ËΩΩ
 * @returns {Promise<string>} ÂΩìÂâçÊï∞ÊçÆÈõÜ
 */
export async function getDatasetAsync() {
  // Â¶ÇÊûúÂΩìÂâçÊï∞ÊçÆÈõÜÂ≠òÂú®ÔºåÁõ¥Êé•ËøîÂõû
  if (currentDataset) {
    return currentDataset;
  }
  
  // Â∞ùËØïÂàùÂßãÂåñÈÖçÁΩÆ
  try {
    await initializeDatasetConfig();
    return getDataset();
  } catch (error) {
    console.error('ÂºÇÊ≠•Ëé∑ÂèñÊï∞ÊçÆÈõÜÂ§±Ë¥•:', error);
    throw new Error('Êó†Ê≥ïËé∑ÂèñÊï∞ÊçÆÈõÜÈÖçÁΩÆ');
  }
}

/**
 * ÂàùÂßãÂåñÊï∞ÊçÆÈõÜÈÖçÁΩÆ
 * @returns {Promise<void>}
 */
export async function initializeDatasetConfig() {
  try {
    console.log('ÂºÄÂßãÂä†ËΩΩÊï∞ÊçÆÈõÜÈÖçÁΩÆ...');
    const config = await loadDatasetsConfig();
    
    console.log('ÈÖçÁΩÆÂä†ËΩΩÁªìÊûú:', {
      hasConfig: !!config,
      defaultDataset: config?.defaultDataset,
      datasetsCount: config?.datasets?.length || 0,
      datasets: config?.datasets?.map(d => d.id) || []
    });
    
    // Êõ¥Êñ∞DatasetTypeÊûö‰∏æ
    const dynamicTypes = getDatasetTypes();
    Object.assign(DatasetType, dynamicTypes);
    
    // ËÆæÁΩÆÈªòËÆ§Êï∞ÊçÆÈõÜÔºàÂ¶ÇÊûúÂΩìÂâçÊï∞ÊçÆÈõÜ‰∏∫Á©∫Ôºâ
    if (!currentDataset) {
      // ‰ºòÂÖà‰ΩøÁî®ÈÖçÁΩÆÊñá‰ª∂‰∏≠ÊåáÂÆöÁöÑÈªòËÆ§Êï∞ÊçÆÈõÜ
      currentDataset = config.defaultDataset;
      console.log('‰ªéÈÖçÁΩÆËÆæÁΩÆÈªòËÆ§Êï∞ÊçÆÈõÜ:', currentDataset);
      
      // Â¶ÇÊûúÈÖçÁΩÆ‰∏≠Ê≤°ÊúâÊåáÂÆöÈªòËÆ§Êï∞ÊçÆÈõÜÔºå‰ΩøÁî®Á¨¨‰∏Ä‰∏™ÂèØÁî®ÁöÑÊï∞ÊçÆÈõÜ
      if (!currentDataset && config.datasets && config.datasets.length > 0) {
        currentDataset = config.datasets[0].id;
        console.warn('ÈÖçÁΩÆÊñá‰ª∂‰∏≠Êú™ÊåáÂÆödefaultDatasetÔºå‰ΩøÁî®Á¨¨‰∏Ä‰∏™Êï∞ÊçÆÈõÜ:', currentDataset);
      }
    }
    
    console.log('Êï∞ÊçÆÈõÜÈÖçÁΩÆÂàùÂßãÂåñÂÆåÊàêÔºåÂΩìÂâçÊï∞ÊçÆÈõÜ:', currentDataset);
    
    // Á°Æ‰øùcurrentDataset‰∏ç‰∏∫null
    if (!currentDataset) {
      console.error('Ë≠¶ÂëäÔºöcurrentDataset‰ªç‰∏∫nullÔºåÂº∫Âà∂ËÆæÁΩÆ‰∏∫Tumour');
      currentDataset = 'Tumour';
    }
    
  } catch (error) {
    console.error('Êï∞ÊçÆÈõÜÈÖçÁΩÆÂàùÂßãÂåñÂ§±Ë¥•:', error);
    console.log('datasetsConfigÁä∂ÊÄÅ:', {
      hasConfig: !!datasetsConfig,
      defaultDataset: datasetsConfig?.defaultDataset,
      datasetsCount: datasetsConfig?.datasets?.length || 0
    });
    
    // Â∞ùËØï‰ªéÂõûÈÄÄÈÖçÁΩÆËé∑ÂèñÈªòËÆ§Êï∞ÊçÆÈõÜ
    if (datasetsConfig && datasetsConfig.defaultDataset) {
      currentDataset = datasetsConfig.defaultDataset;
      console.log('‰ªéÂõûÈÄÄÈÖçÁΩÆËÆæÁΩÆÈªòËÆ§Êï∞ÊçÆÈõÜ:', currentDataset);
    } else if (datasetsConfig && datasetsConfig.datasets && datasetsConfig.datasets.length > 0) {
      currentDataset = datasetsConfig.datasets[0].id;
      console.log('‰ªéÂõûÈÄÄÈÖçÁΩÆÁ¨¨‰∏Ä‰∏™Êï∞ÊçÆÈõÜËÆæÁΩÆ:', currentDataset);
    } else {
      // ÊúÄÂêéÁöÑÂÖúÂ∫ïÔºöÂº∫Âà∂ËÆæÁΩÆ‰∏Ä‰∏™ÈªòËÆ§ÂÄº
      console.error('Êó†Ê≥ïÁ°ÆÂÆöÈªòËÆ§Êï∞ÊçÆÈõÜÔºöÊ≤°ÊúâÂèØÁî®ÁöÑÈÖçÁΩÆ‰ø°ÊÅØÔºåÂº∫Âà∂ËÆæÁΩÆ‰∏∫Tumour');
      currentDataset = 'Tumour';
    }
  }
}

/**
 * Ëé∑ÂèñÊï∞ÊçÆÈõÜÁöÑÂü∫Á°ÄË∑ØÂæÑ
 * @param {string} [datasetType] - ÂèØÈÄâÁöÑÊï∞ÊçÆÈõÜÁ±ªÂûãÔºåÈªòËÆ§‰ΩøÁî®ÂΩìÂâçÊï∞ÊçÆÈõÜ
 * @returns {string} Êï∞ÊçÆÈõÜÂü∫Á°ÄË∑ØÂæÑ
 */
export function getDatasetBasePath(datasetType = null) {
  const dataset = datasetType || getDataset();
  console.log('getDatasetBasePath - dataset:', dataset);
  
  // üîß Â¶ÇÊûúÊï∞ÊçÆÈõÜ‰∏∫Á©∫ÔºåÂ∞ùËØï‰ªéÈÖçÁΩÆËé∑ÂèñÈªòËÆ§Êï∞ÊçÆÈõÜ
  if (!dataset && datasetsConfig) {
    const fallbackDataset = datasetsConfig.defaultDataset || 
                           (datasetsConfig.datasets?.length > 0 ? datasetsConfig.datasets[0].id : null);
    if (fallbackDataset) {
      console.warn('‰ΩøÁî®ÂõûÈÄÄÊï∞ÊçÆÈõÜ:', fallbackDataset);
      return getDatasetBasePath(fallbackDataset);
    }
  }
  
  // Â¶ÇÊûú‰ªçÁÑ∂‰∏∫Á©∫Ôºå‰ΩøÁî®Á°¨ÁºñÁ†ÅÁöÑÈªòËÆ§ÂÄº‰Ωú‰∏∫ÊúÄÂêéÂõûÈÄÄ
  if (!dataset) {
    console.error('Êó†Ê≥ïËé∑ÂèñÊï∞ÊçÆÈõÜÔºå‰ΩøÁî®ÈªòËÆ§ÂõûÈÄÄË∑ØÂæÑ: /Tumour');
    return '/Tumour';
  }
  
  // ‰ºòÂÖà‰ªéÈÖçÁΩÆËé∑ÂèñbasePath
  const datasetInfo = getDatasetInfo(dataset);
  if (datasetInfo && datasetInfo.basePath) {
    return datasetInfo.basePath;
  }
  
  // ÂõûÈÄÄÂà∞ÈªòËÆ§Ë∑ØÂæÑÊ†ºÂºè
  return `/${dataset}`;
}

/**
 * ÊûÑÂª∫Êï∞ÊçÆÈõÜÁõ∏ÂÖ≥ÁöÑÊñá‰ª∂Ë∑ØÂæÑ
 * @param {string} relativePath - Áõ∏ÂØπË∑ØÂæÑ
 * @param {string} [datasetType] - ÂèØÈÄâÁöÑÊï∞ÊçÆÈõÜÁ±ªÂûãÔºåÈªòËÆ§‰ΩøÁî®ÂΩìÂâçÊï∞ÊçÆÈõÜ
 * @returns {string} ÂÆåÊï¥ÁöÑÊñá‰ª∂Ë∑ØÂæÑ
 */
export function buildDatasetPath(relativePath, datasetType = null) {
  if (!relativePath) {
    console.error('Áõ∏ÂØπË∑ØÂæÑ‰∏çËÉΩ‰∏∫Á©∫');
    return '/Tumour/unknown';  // ÂÆâÂÖ®ÁöÑÂõûÈÄÄË∑ØÂæÑ
  }
  
  const basePath = getDatasetBasePath(datasetType);
  const fullPath = `${basePath}/${relativePath}`;
  
  console.log(`buildDatasetPath: ${relativePath} -> ${fullPath}`);
  return fullPath;
}

/**
 * ÂºÇÊ≠•ÊûÑÂª∫Êï∞ÊçÆÈõÜÁõ∏ÂÖ≥ÁöÑÊñá‰ª∂Ë∑ØÂæÑÔºåÁ°Æ‰øùÈÖçÁΩÆÂ∑≤Âä†ËΩΩ
 * @param {string} relativePath - Áõ∏ÂØπË∑ØÂæÑ
 * @param {string} [datasetType] - ÂèØÈÄâÁöÑÊï∞ÊçÆÈõÜÁ±ªÂûãÔºåÈªòËÆ§‰ΩøÁî®ÂΩìÂâçÊï∞ÊçÆÈõÜ
 * @returns {Promise<string>} ÂÆåÊï¥ÁöÑÊñá‰ª∂Ë∑ØÂæÑ
 */
export async function buildDatasetPathAsync(relativePath, datasetType = null) {
  if (!relativePath) {
    throw new Error('Áõ∏ÂØπË∑ØÂæÑ‰∏çËÉΩ‰∏∫Á©∫');
  }
  
  try {
    // Á°Æ‰øùÈÖçÁΩÆÂ∑≤Âä†ËΩΩ
    await initializeDatasetConfig();
    
    const dataset = datasetType || await getDatasetAsync();
    if (!dataset) {
      throw new Error('Êó†Ê≥ïËé∑ÂèñÊï∞ÊçÆÈõÜ‰ø°ÊÅØ');
    }
    
    // ‰ºòÂÖà‰ªéÈÖçÁΩÆËé∑ÂèñbasePath
    const datasetInfo = getDatasetInfo(dataset);
    const basePath = datasetInfo?.basePath || `/${dataset}`;
    
    return `${basePath}/${relativePath}`;
  } catch (error) {
    console.error('ÂºÇÊ≠•ÊûÑÂª∫Êï∞ÊçÆÈõÜË∑ØÂæÑÂ§±Ë¥•:', error.message);
    throw new Error(`Êó†Ê≥ïÊûÑÂª∫Êï∞ÊçÆÈõÜË∑ØÂæÑ: ${error.message}`);
  }
}

