<template>
  <v-app>
    <v-overlay :value="isLoading" absolute>
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
    
    <v-snackbar
      v-model="showError"
      :timeout="5000"
      color="error"
      top
    >
      {{ errorMessage }}
      <template v-slot:action="{ attrs }">
        <v-btn
          text
          v-bind="attrs"
          @click="clearError"
        >
          ÂÖ≥Èó≠
        </v-btn>
      </template>
    </v-snackbar>
    
    <v-main>
      <router-view/>
    </v-main>
  </v-app>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'

export default {
  data: () => ({
    showError: false
  }),
  computed: {
    ...mapState({
      errorMessage: state => state.error
    }),
    ...mapGetters([
      'isLoading',
      'hasError'
    ])
  },
  watch: {
    hasError(newVal) {
      this.showError = newVal;
    }
  },
  methods: {
    ...mapActions([
      'clearError'
    ])
  },
  created() {
    // Ê∑ªÂä†ÂÖ®Â±ÄÈîôËØØÂ§ÑÁêÜ
    window.addEventListener('error', (event) => {
      this.$store.dispatch('setError', `Á≥ªÁªüÈîôËØØ: ${event.message}`);
    });
  }
}
</script>

