/**
 * ÈùûÂìçÂ∫îÂºèÊï∞ÊçÆÂ≠òÂÇ®ÁÆ°ÁêÜÂô®
 * ‰∏ìÈó®Áî®‰∫éÂ≠òÂÇ®Â§ßÈáèÊï∞ÊçÆÔºåÈÅøÂÖçVueÂìçÂ∫îÂºèÁ≥ªÁªüÁöÑÂÜÖÂ≠òÊ∫¢Âá∫ÈóÆÈ¢ò
 * 
 * Ëß£ÂÜ≥ÊñπÊ°àËØ¥ÊòéÔºö
 * - Vue 2ÁöÑÂìçÂ∫îÂºèÁ≥ªÁªü‰ºöÂØπÊï∞ÁªÑÁöÑÊØè‰∏™ÂÖÉÁ¥†Âª∫Á´ã‰æùËµñËøΩË∏™(dependArray)
 * - ÂØπ‰∫é225‰∏á√ó32Â≠óÊÆµÁöÑÊï∞ÊçÆÔºåËøô‰ºöÂØºËá¥‰∏•ÈáçÁöÑÂÜÖÂ≠òÊ∫¢Âá∫
 * - Ê≠§Ê®°ÂùóÂ∞ÜÂ§ßÊï∞ÊçÆÂ≠òÂÇ®Âú®VueÂìçÂ∫îÂºèÁ≥ªÁªü‰πãÂ§ñÔºåÈÄöËøá‰∫ã‰ª∂Á≥ªÁªüÈÄöÁü•ÁªÑ‰ª∂Êõ¥Êñ∞
 */

/**
 * ÈùûÂìçÂ∫îÂºèÊï∞ÊçÆÂ≠òÂÇ®Á±ª
 * ‰ΩøÁî®Object.freeze()ÂíåWeakMapÁ°Æ‰øùÊï∞ÊçÆ‰∏ç‰ºöË¢´VueËΩ¨Êç¢‰∏∫ÂìçÂ∫îÂºè
 */
class NonReactiveDataStore {
  constructor() {
    // ‰ΩøÁî®MapÂ≠òÂÇ®Êï∞ÊçÆÔºåÈÅøÂÖçVueÁöÑÂìçÂ∫îÂºèËΩ¨Êç¢
    this.dataMap = new Map();
    
    // ‰∫ã‰ª∂ÁõëÂê¨Âô®Êò†Â∞Ñ
    this.listeners = new Map();
    
    // Êï∞ÊçÆÁâàÊú¨Âè∑ÔºåÁî®‰∫éËß¶ÂèëÁªÑ‰ª∂Êõ¥Êñ∞
    this.version = 0;
    
    console.log('üöÄ NonReactiveDataStore initialized - Â§ßÊï∞ÊçÆÂ∞ÜÂ≠òÂÇ®Âú®VueÂìçÂ∫îÂºèÁ≥ªÁªü‰πãÂ§ñ');
  }

  /**
   * Â≠òÂÇ®Êï∞ÊçÆ
   * @param {string} key - Êï∞ÊçÆÈîÆÂêç
   * @param {*} data - Ë¶ÅÂ≠òÂÇ®ÁöÑÊï∞ÊçÆÔºàÂ∞ÜË¢´ÂÜªÁªì‰ª•ÈÅøÂÖçÂìçÂ∫îÂºèËΩ¨Êç¢Ôºâ
   */
  setData(key, data) {
    // Ê∑±Â∫¶ÂÜªÁªìÊï∞ÊçÆÔºåÁ°Æ‰øùVueÊó†Ê≥ïÂ∞ÜÂÖ∂ËΩ¨Êç¢‰∏∫ÂìçÂ∫îÂºè
    const frozenData = this.deepFreeze(data);
    this.dataMap.set(key, frozenData);
    
    // Êõ¥Êñ∞ÁâàÊú¨Âè∑
    this.version++;
    
    console.log(`üì¶ NonReactiveDataStore.setData: ${key}, size: ${this.getDataSize(data)}, version: ${this.version}`);
    
    // ÈÄöÁü•ÁõëÂê¨Âô®
    this.notifyListeners(key, frozenData);
    
    // ÈÄöÁü•ÁâàÊú¨Êõ¥Êñ∞ÁõëÂê¨Âô®
    this.notifyListeners('__version__', this.version);
  }

  /**
   * Ëé∑ÂèñÊï∞ÊçÆ
   * @param {string} key - Êï∞ÊçÆÈîÆÂêç
   * @returns {*} Â≠òÂÇ®ÁöÑÊï∞ÊçÆ
   */
  getData(key) {
    return this.dataMap.get(key);
  }

  /**
   * Ê£ÄÊü•Êï∞ÊçÆÊòØÂê¶Â≠òÂú®
   * @param {string} key - Êï∞ÊçÆÈîÆÂêç
   * @returns {boolean} ÊòØÂê¶Â≠òÂú®
   */
  hasData(key) {
    return this.dataMap.has(key);
  }

  /**
   * Âà†Èô§Êï∞ÊçÆ
   * @param {string} key - Êï∞ÊçÆÈîÆÂêç
   */
  removeData(key) {
    const removed = this.dataMap.delete(key);
    if (removed) {
      this.version++;
      this.notifyListeners(key, null);
      // ÈÄöÁü•ÁâàÊú¨Êõ¥Êñ∞ÁõëÂê¨Âô®
      this.notifyListeners('__version__', this.version);
      console.log(`üóëÔ∏è NonReactiveDataStore.removeData: ${key}, version: ${this.version}`);
    }
  }

  /**
   * Ê∏ÖÁ©∫ÊâÄÊúâÊï∞ÊçÆ
   */
  clearAll() {
    this.dataMap.clear();
    this.version++;
    // ÈÄöÁü•ÁâàÊú¨Êõ¥Êñ∞ÁõëÂê¨Âô®
    this.notifyListeners('__version__', this.version);
    console.log(`üßπ NonReactiveDataStore.clearAll, version: ${this.version}`);
  }

  /**
   * Ëé∑ÂèñÂΩìÂâçÁâàÊú¨Âè∑ÔºàÁî®‰∫éËß¶ÂèëVueÁªÑ‰ª∂Êõ¥Êñ∞Ôºâ
   * @returns {number} ÁâàÊú¨Âè∑
   */
  getVersion() {
    return this.version;
  }

  /**
   * Ê∑±Â∫¶ÂÜªÁªìÂØπË±°ÔºåÈò≤Ê≠¢VueÂìçÂ∫îÂºèËΩ¨Êç¢
   * @param {*} obj - Ë¶ÅÂÜªÁªìÁöÑÂØπË±°
   * @returns {*} ÂÜªÁªìÂêéÁöÑÂØπË±°
   */
  deepFreeze(obj) {
    if (obj === null || typeof obj !== 'object') {
      return obj;
    }

    // Â¶ÇÊûúÂ∑≤ÁªèË¢´ÂÜªÁªìÔºåÁõ¥Êé•ËøîÂõû
    if (Object.isFrozen(obj)) {
      return obj;
    }

    // ÂØπ‰∫éÊï∞ÁªÑÔºåÂÖàÂÜªÁªìÊØè‰∏™ÂÖÉÁ¥†
    if (Array.isArray(obj)) {
      obj.forEach((item, index) => {
        obj[index] = this.deepFreeze(item);
      });
    } else {
      // ÂØπ‰∫éÂØπË±°ÔºåÂÖàÂÜªÁªìÊØè‰∏™Â±ûÊÄßÂÄº
      Object.values(obj).forEach(value => {
        this.deepFreeze(value);
      });
    }

    // ÂÜªÁªìÂØπË±°Êú¨Ë∫´
    return Object.freeze(obj);
  }

  /**
   * Ê∑ªÂä†Êï∞ÊçÆÂèòÊõ¥ÁõëÂê¨Âô®
   * @param {string} key - ÁõëÂê¨ÁöÑÊï∞ÊçÆÈîÆÂêç
   * @param {Function} listener - ÁõëÂê¨Âô®ÂáΩÊï∞
   */
  addListener(key, listener) {
    if (!this.listeners.has(key)) {
      this.listeners.set(key, new Set());
    }
    this.listeners.get(key).add(listener);
  }

  /**
   * ÁßªÈô§Êï∞ÊçÆÂèòÊõ¥ÁõëÂê¨Âô®
   * @param {string} key - ÁõëÂê¨ÁöÑÊï∞ÊçÆÈîÆÂêç
   * @param {Function} listener - ÁõëÂê¨Âô®ÂáΩÊï∞
   */
  removeListener(key, listener) {
    const keyListeners = this.listeners.get(key);
    if (keyListeners) {
      keyListeners.delete(listener);
      if (keyListeners.size === 0) {
        this.listeners.delete(key);
      }
    }
  }

  /**
   * ÈÄöÁü•ÁõëÂê¨Âô®Êï∞ÊçÆÂèòÊõ¥
   * @param {string} key - ÂèòÊõ¥ÁöÑÊï∞ÊçÆÈîÆÂêç
   * @param {*} data - Êñ∞Êï∞ÊçÆ
   */
  notifyListeners(key, data) {
    const keyListeners = this.listeners.get(key);
    if (keyListeners) {
      keyListeners.forEach(listener => {
        try {
          listener(data, key);
        } catch (error) {
          console.error(`ÁõëÂê¨Âô®ÊâßË°åÈîôËØØ [${key}]:`, error);
        }
      });
    }
  }

  /**
   * Ëé∑ÂèñÊï∞ÊçÆÂ§ßÂ∞è‰ø°ÊÅØÔºàÁî®‰∫éË∞ÉËØïÔºâ
   * @param {*} data - Êï∞ÊçÆ
   * @returns {string} Â§ßÂ∞èÊèèËø∞
   */
  getDataSize(data) {
    if (Array.isArray(data)) {
      return `Array[${data.length}]`;
    } else if (data && typeof data === 'object') {
      return `Object{${Object.keys(data).length} keys}`;
    } else {
      return typeof data;
    }
  }

  /**
   * Ëé∑ÂèñÂ≠òÂÇ®ÁªüËÆ°‰ø°ÊÅØ
   * @returns {Object} ÁªüËÆ°‰ø°ÊÅØ
   */
  getStats() {
    const stats = {
      totalKeys: this.dataMap.size,
      version: this.version,
      keys: Array.from(this.dataMap.keys()),
      sizes: {}
    };

    this.dataMap.forEach((data, key) => {
      stats.sizes[key] = this.getDataSize(data);
    });

    return stats;
  }
}

// ÂàõÂª∫ÂÖ®Â±ÄÂçï‰æãÂÆû‰æã
const nonReactiveDataStore = new NonReactiveDataStore();

// Âú®ÂºÄÂèëÁéØÂ¢É‰∏ãÔºåÂ∞ÜÂÆû‰æãÊåÇËΩΩÂà∞windowÂØπË±°‰ª•‰æøË∞ÉËØï
if (process.env.NODE_ENV === 'development') {
  window.nonReactiveDataStore = nonReactiveDataStore;
}

export default nonReactiveDataStore;

/**
 * Vue mixinÔºåÁî®‰∫éÂú®ÁªÑ‰ª∂‰∏≠Êñπ‰æøÂú∞ËÆøÈóÆÈùûÂìçÂ∫îÂºèÊï∞ÊçÆ
 * 
 * ‰ΩøÁî®ÊñπÊ≥ïÔºö
 * mixins: [nonReactiveDataMixin]
 * 
 * ÁÑ∂ÂêéÂú®ÁªÑ‰ª∂‰∏≠ÂèØ‰ª•‰ΩøÁî®Ôºö
 * - this.$nonReactiveData.setData(key, data)
 * - this.$nonReactiveData.getData(key)
 * - this.$nonReactiveData.watchData(key, callback)
 */
export const nonReactiveDataMixin = {
  data() {
    return {
      // Ê∑ªÂä†ÁâàÊú¨Âè∑Âà∞ÂìçÂ∫îÂºèÊï∞ÊçÆ‰∏≠ÔºåÁî®‰∫éËß¶ÂèëÈáçÊñ∞Ê∏≤Êüì
      nonReactiveDataVersion: nonReactiveDataStore.getVersion()
    };
  },

  created() {
    // Â∞ÜÊï∞ÊçÆÂ≠òÂÇ®ÂÆû‰æãÊåÇËΩΩÂà∞ÁªÑ‰ª∂ÂÆû‰æã
    this.$nonReactiveData = nonReactiveDataStore;
    
    // ÁõëÂê¨ÁâàÊú¨ÂèòÊõ¥
    this._versionUpdateHandler = () => {
      this.nonReactiveDataVersion = nonReactiveDataStore.getVersion();
    };
    
    nonReactiveDataStore.addListener('__version__', this._versionUpdateHandler);
  },

  beforeDestroy() {
    // Ê∏ÖÁêÜÁâàÊú¨ÁõëÂê¨Âô®
    if (this._versionUpdateHandler) {
      nonReactiveDataStore.removeListener('__version__', this._versionUpdateHandler);
    }
  },

  methods: {
    /**
     * Ëé∑ÂèñÈùûÂìçÂ∫îÂºèÊï∞ÊçÆÁöÑ‰æøÊç∑ÊñπÊ≥ï
     * @param {string} key - Êï∞ÊçÆÈîÆÂêç
     * @returns {*} Â≠òÂÇ®ÁöÑÊï∞ÊçÆ
     */
    getNonReactiveData(key) {
      // Ëß¶ÂèëÂìçÂ∫îÂºèÊõ¥Êñ∞ÁöÑÁâàÊú¨Âè∑‰æùËµñ
      this.nonReactiveDataVersion;
      const data = nonReactiveDataStore.getData(key);
      
      // Ê∑ªÂä†Ë∞ÉËØï‰ø°ÊÅØ
      if (key === 'geneExp') {
        console.log(`üîç getNonReactiveData(${key}):`, {
          version: this.nonReactiveDataVersion,
          hasData: !!data,
          geneCount: data ? Object.keys(data).length : 0,
          genes: data ? Object.keys(data) : []
        });
      }
      
      return data;
    },

    /**
     * ËÆæÁΩÆÈùûÂìçÂ∫îÂºèÊï∞ÊçÆÁöÑ‰æøÊç∑ÊñπÊ≥ï
     * @param {string} key - Êï∞ÊçÆÈîÆÂêç
     * @param {*} data - Ë¶ÅÂ≠òÂÇ®ÁöÑÊï∞ÊçÆ
     */
    setNonReactiveData(key, data) {
      nonReactiveDataStore.setData(key, data);
    },

    /**
     * ÁõëÂê¨ÈùûÂìçÂ∫îÂºèÊï∞ÊçÆÂèòÊõ¥
     * @param {string} key - Êï∞ÊçÆÈîÆÂêç
     * @param {Function} callback - ÂõûË∞ÉÂáΩÊï∞
     */
    watchNonReactiveData(key, callback) {
      nonReactiveDataStore.addListener(key, callback);
      
      // Âú®ÁªÑ‰ª∂ÈîÄÊØÅÊó∂Ëá™Âä®Ê∏ÖÁêÜÁõëÂê¨Âô®
      this.$once('hook:beforeDestroy', () => {
        nonReactiveDataStore.removeListener(key, callback);
      });
    }
  }
};
