/**
 * ÂÜÖÂ≠òÊµãËØïËæÖÂä©Â∑•ÂÖ∑
 * Áî®‰∫éÊµãËØïVueÂìçÂ∫îÂºèÁ≥ªÁªüÂÜÖÂ≠ò‰ºòÂåñÊïàÊûú
 */

/**
 * Ëé∑ÂèñÂΩìÂâçÂÜÖÂ≠ò‰ΩøÁî®ÊÉÖÂÜµ
 * @returns {Object} ÂÜÖÂ≠ò‰ΩøÁî®‰ø°ÊÅØ
 */
export function getMemoryUsage() {
  if (performance && performance.memory) {
    return {
      usedJSHeapSize: Math.round(performance.memory.usedJSHeapSize / 1024 / 1024), // MB
      totalJSHeapSize: Math.round(performance.memory.totalJSHeapSize / 1024 / 1024), // MB
      jsHeapSizeLimit: Math.round(performance.memory.jsHeapSizeLimit / 1024 / 1024), // MB
      timestamp: Date.now()
    };
  }
  return {
    usedJSHeapSize: 'N/A',
    totalJSHeapSize: 'N/A', 
    jsHeapSizeLimit: 'N/A',
    timestamp: Date.now()
  };
}

/**
 * ËÆ∞ÂΩïÂÜÖÂ≠ò‰ΩøÁî®ÊÉÖÂÜµ
 * @param {string} label - ËÆ∞ÂΩïÊ†áÁ≠æ
 */
export function logMemoryUsage(label) {
  const memory = getMemoryUsage();
  console.log(`üîç ÂÜÖÂ≠ò‰ΩøÁî®ÊÉÖÂÜµ [${label}]:`, {
    Â∑≤‰ΩøÁî®: `${memory.usedJSHeapSize} MB`,
    ÊÄªÂàÜÈÖç: `${memory.totalJSHeapSize} MB`,
    ÈôêÂà∂: `${memory.jsHeapSizeLimit} MB`,
    ‰ΩøÁî®Áéá: memory.usedJSHeapSize !== 'N/A' 
      ? `${Math.round((memory.usedJSHeapSize / memory.jsHeapSizeLimit) * 100)}%`
      : 'N/A'
  });
  return memory;
}

/**
 * ÂÜÖÂ≠òÁõëÊéßÂô®Á±ª
 */
export class MemoryMonitor {
  constructor() {
    this.records = [];
    this.isMonitoring = false;
    this.intervalId = null;
  }

  /**
   * ÂºÄÂßãÁõëÊéßÂÜÖÂ≠ò‰ΩøÁî®
   * @param {number} interval - ÁõëÊéßÈó¥ÈöîÔºàÊØ´ÁßíÔºâ
   */
  start(interval = 5000) {
    if (this.isMonitoring) {
      console.warn('ÂÜÖÂ≠òÁõëÊéßÂ∑≤Âú®ËøêË°å‰∏≠');
      return;
    }

    this.isMonitoring = true;
    console.log('üöÄ ÂºÄÂßãÂÜÖÂ≠òÁõëÊéßÔºåÈó¥Èöî:', interval, 'ms');
    
    // Á´ãÂç≥ËÆ∞ÂΩï‰∏ÄÊ¨°
    this.record('ÁõëÊéßÂºÄÂßã');

    this.intervalId = setInterval(() => {
      this.record('ÂÆöÊúüÊ£ÄÊü•');
    }, interval);
  }

  /**
   * ÂÅúÊ≠¢ÁõëÊéß
   */
  stop() {
    if (!this.isMonitoring) {
      console.warn('ÂÜÖÂ≠òÁõëÊéßÊú™ËøêË°å');
      return;
    }

    this.isMonitoring = false;
    if (this.intervalId) {
      clearInterval(this.intervalId);
      this.intervalId = null;
    }

    console.log('üõë ÂÜÖÂ≠òÁõëÊéßÂ∑≤ÂÅúÊ≠¢');
    this.showSummary();
  }

  /**
   * ËÆ∞ÂΩïÂÜÖÂ≠ò‰ΩøÁî®ÊÉÖÂÜµ
   * @param {string} label - ËÆ∞ÂΩïÊ†áÁ≠æ
   */
  record(label) {
    const memory = getMemoryUsage();
    const record = {
      label,
      ...memory,
      time: new Date().toLocaleTimeString()
    };

    this.records.push(record);
    
    // Ê£ÄÊü•ÂÜÖÂ≠ò‰ΩøÁî®ÊòØÂê¶ËøáÈ´ò
    if (memory.usedJSHeapSize !== 'N/A' && memory.usedJSHeapSize > 1000) {
      console.warn(`‚ö†Ô∏è ÂÜÖÂ≠ò‰ΩøÁî®ËøáÈ´ò [${label}]: ${memory.usedJSHeapSize} MB`);
    }

    return record;
  }

  /**
   * ÊòæÁ§∫ÁõëÊéßÊëòË¶Å
   */
  showSummary() {
    if (this.records.length === 0) {
      console.log('üìä ÊöÇÊó†ÂÜÖÂ≠òÁõëÊéßËÆ∞ÂΩï');
      return;
    }

    const validRecords = this.records.filter(r => r.usedJSHeapSize !== 'N/A');
    
    if (validRecords.length === 0) {
      console.log('üìä ÊµèËßàÂô®‰∏çÊîØÊåÅÂÜÖÂ≠òÁõëÊéß');
      return;
    }

    const maxMemory = Math.max(...validRecords.map(r => r.usedJSHeapSize));
    const minMemory = Math.min(...validRecords.map(r => r.usedJSHeapSize));
    const avgMemory = Math.round(
      validRecords.reduce((sum, r) => sum + r.usedJSHeapSize, 0) / validRecords.length
    );

    console.log('üìä ÂÜÖÂ≠òÁõëÊéßÊëòË¶Å:', {
      ÊÄªËÆ∞ÂΩïÊï∞: this.records.length,
      ÊúâÊïàËÆ∞ÂΩïÊï∞: validRecords.length,
      ÊúÄÂ§ßÂÜÖÂ≠ò: `${maxMemory} MB`,
      ÊúÄÂ∞èÂÜÖÂ≠ò: `${minMemory} MB`,
      Âπ≥ÂùáÂÜÖÂ≠ò: `${avgMemory} MB`,
      ÂÜÖÂ≠òÊ≥¢Âä®: `${maxMemory - minMemory} MB`
    });

    // ÊòæÁ§∫ËØ¶ÁªÜËÆ∞ÂΩïÔºàÊúÄËøë10Êù°Ôºâ
    const recentRecords = this.records.slice(-10);
    console.table(recentRecords.map(r => ({
      Êó∂Èó¥: r.time,
      Ê†áÁ≠æ: r.label,
      Â∑≤‰ΩøÁî®: `${r.usedJSHeapSize} MB`,
      ÊÄªÂàÜÈÖç: `${r.totalJSHeapSize} MB`
    })));
  }

  /**
   * Ëé∑ÂèñÊâÄÊúâËÆ∞ÂΩï
   */
  getRecords() {
    return [...this.records];
  }

  /**
   * Ê∏ÖÁ©∫ËÆ∞ÂΩï
   */
  clearRecords() {
    this.records = [];
    console.log('üßπ ÂÜÖÂ≠òÁõëÊéßËÆ∞ÂΩïÂ∑≤Ê∏ÖÁ©∫');
  }
}

/**
 * ÊµãËØïVueÂìçÂ∫îÂºèÁ≥ªÁªüÂÜÖÂ≠ò‰ΩøÁî®
 * @param {Array} testData - ÊµãËØïÊï∞ÊçÆ
 * @param {Object} vueInstance - VueÂÆû‰æã
 */
export function testVueReactivityMemory(testData, vueInstance) {
  console.log('üß™ ÂºÄÂßãVueÂìçÂ∫îÂºèÁ≥ªÁªüÂÜÖÂ≠òÊµãËØï');
  
  const beforeMemory = logMemoryUsage('ÊµãËØïÂâç');
  
  // Ê®°ÊãüÂ∞ÜÂ§ßÊï∞ÊçÆÊ∑ªÂä†Âà∞VueÂìçÂ∫îÂºèÁ≥ªÁªü
  if (vueInstance && vueInstance.$data) {
    console.log('‚ö†Ô∏è Ë≠¶ÂëäÔºöÂç≥Â∞ÜÂ∞ÜÂ§ßÊï∞ÊçÆÊ∑ªÂä†Âà∞VueÂìçÂ∫îÂºèÁ≥ªÁªüÔºåÂèØËÉΩÂØºËá¥ÂÜÖÂ≠òÊ∫¢Âá∫');
    
    try {
      // ËøôÈáåÊïÖÊÑè‰∏çÊâßË°åÔºåÂõ†‰∏∫‰ºöÂØºËá¥OOM
      // vueInstance.$data.testLargeData = testData;
      console.log('‚úÖ Ë∑≥Ëøá‰∫ÜÂç±Èô©ÁöÑÂìçÂ∫îÂºèÊï∞ÊçÆËÆæÁΩÆÔºåÈÅøÂÖçÂÜÖÂ≠òÊ∫¢Âá∫');
    } catch (error) {
      console.error('‚ùå VueÂìçÂ∫îÂºèÁ≥ªÁªüÂÜÖÂ≠òÊµãËØïÂ§±Ë¥•:', error);
    }
  }
  
  const afterMemory = logMemoryUsage('ÊµãËØïÂêé');
  
  console.log('üß™ VueÂìçÂ∫îÂºèÁ≥ªÁªüÂÜÖÂ≠òÊµãËØïÂÆåÊàê');
  return {
    before: beforeMemory,
    after: afterMemory,
    difference: afterMemory.usedJSHeapSize !== 'N/A' && beforeMemory.usedJSHeapSize !== 'N/A'
      ? afterMemory.usedJSHeapSize - beforeMemory.usedJSHeapSize
      : 'N/A'
  };
}

/**
 * ÊµãËØïÈùûÂìçÂ∫îÂºèÊï∞ÊçÆÂ≠òÂÇ®ÂÜÖÂ≠ò‰ΩøÁî®
 * @param {Array} testData - ÊµãËØïÊï∞ÊçÆ
 */
export function testNonReactiveMemory(testData) {
  console.log('üß™ ÂºÄÂßãÈùûÂìçÂ∫îÂºèÊï∞ÊçÆÂ≠òÂÇ®ÂÜÖÂ≠òÊµãËØï');
  
  const beforeMemory = logMemoryUsage('ÈùûÂìçÂ∫îÂºèÊµãËØïÂâç');
  
  try {
    // ‰ΩøÁî®Object.freezeÂÜªÁªìÊï∞ÊçÆ
    const frozenData = Object.freeze([...testData]);
    console.log('‚úÖ ÊàêÂäüÂàõÂª∫ÂÜªÁªìÊï∞ÊçÆÔºåÈïøÂ∫¶:', frozenData.length);
    
    // Ê®°ÊãüÂ≠òÂÇ®Âà∞Map‰∏≠
    const dataMap = new Map();
    dataMap.set('testData', frozenData);
    
    console.log('‚úÖ ÊàêÂäüÂ≠òÂÇ®Âà∞ÈùûÂìçÂ∫îÂºèMap‰∏≠');
    
    // Ê∏ÖÁêÜ
    dataMap.clear();
    
  } catch (error) {
    console.error('‚ùå ÈùûÂìçÂ∫îÂºèÊï∞ÊçÆÂ≠òÂÇ®ÊµãËØïÂ§±Ë¥•:', error);
  }
  
  const afterMemory = logMemoryUsage('ÈùûÂìçÂ∫îÂºèÊµãËØïÂêé');
  
  console.log('üß™ ÈùûÂìçÂ∫îÂºèÊï∞ÊçÆÂ≠òÂÇ®ÂÜÖÂ≠òÊµãËØïÂÆåÊàê');
  return {
    before: beforeMemory,
    after: afterMemory,
    difference: afterMemory.usedJSHeapSize !== 'N/A' && beforeMemory.usedJSHeapSize !== 'N/A'
      ? afterMemory.usedJSHeapSize - beforeMemory.usedJSHeapSize
      : 'N/A'
  };
}

// ÂàõÂª∫ÂÖ®Â±ÄÂÜÖÂ≠òÁõëÊéßÂô®ÂÆû‰æã
export const globalMemoryMonitor = new MemoryMonitor();

// Âú®ÂºÄÂèëÁéØÂ¢É‰∏ãÔºåÂ∞ÜÂ∑•ÂÖ∑ÊåÇËΩΩÂà∞windowÂØπË±°
if (process.env.NODE_ENV === 'development') {
  window.memoryTestHelper = {
    getMemoryUsage,
    logMemoryUsage,
    MemoryMonitor,
    testVueReactivityMemory,
    testNonReactiveMemory,
    globalMemoryMonitor
  };
  
  console.log('üîß ÂÜÖÂ≠òÊµãËØïÂ∑•ÂÖ∑Â∑≤ÊåÇËΩΩÂà∞ window.memoryTestHelper');
}
