/**
 * AxiosÂÆû‰æãÈÖçÁΩÆ
 */
import axios from 'axios';

// ÂàõÂª∫axiosÂÆû‰æã
const api = axios.create({
  baseURL: '/',
  timeout: 30000,
  headers: {
    'Content-Type': 'application/json'
  }
});

// ËØ∑Ê±ÇÊã¶Êà™Âô®
api.interceptors.request.use(
  config => {
    // Âú®ÂèëÈÄÅËØ∑Ê±Ç‰πãÂâçÂÅö‰∫õ‰ªÄ‰πà
    return config;
  },
  error => {
    // ÂØπËØ∑Ê±ÇÈîôËØØÂÅö‰∫õ‰ªÄ‰πà
    console.error('ËØ∑Ê±ÇÈîôËØØ:', error);
    return Promise.reject(error);
  }
);

// ÂìçÂ∫îÊã¶Êà™Âô®
api.interceptors.response.use(
  response => {
    // ÂØπÂìçÂ∫îÊï∞ÊçÆÂÅö‰∫õ‰ªÄ‰πà
    return response.data;
  },
  error => {
    // ÂØπÂìçÂ∫îÈîôËØØÂÅö‰∫õ‰ªÄ‰πà
    console.error('ÂìçÂ∫îÈîôËØØ:', error);
    
    // Ê†πÊçÆÈîôËØØÁä∂ÊÄÅÁ†ÅÂÅö‰∏çÂêåÂ§ÑÁêÜ
    if (error.response) {
      switch (error.response.status) {
        case 404:
          console.error('ËØ∑Ê±ÇÁöÑËµÑÊ∫ê‰∏çÂ≠òÂú®');
          break;
        case 500:
          console.error('ÊúçÂä°Âô®ÂÜÖÈÉ®ÈîôËØØ');
          break;
        default:
          console.error(`ËØ∑Ê±ÇÂ§±Ë¥•: ${error.response.status}`);
      }
    } else if (error.request) {
      console.error('Ê≤°ÊúâÊî∂Âà∞ÂìçÂ∫î');
    } else {
      console.error('ËØ∑Ê±ÇÈÖçÁΩÆÈîôËØØ');
    }
    
    return Promise.reject(error);
  }
);

export { api }; 