import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from '@/plugins/vuetify' 
import "./assets/css/tab.css"
import _ from 'lodash'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import axios from 'axios'
import { setDataSource, DataSourceType, initializeDatasetConfig } from '@/api/config'
import { logMemoryUsage, globalMemoryMonitor } from '@/utils/memoryTestHelper'

// Êï∞ÊçÆÊ∫êÈÖçÁΩÆ - ‰øÆÊîπËøôÈáåÂèØ‰ª•ÂàáÊç¢Êï∞ÊçÆÊ∫ê
// ‰ΩøÁî® DataSourceType.LOCAL ‰ªépublicÊñá‰ª∂Â§πËé∑ÂèñÊï∞ÊçÆ
// ‰ΩøÁî® DataSourceType.REMOTE ‰ªéËøúÁ®ãAPIËé∑ÂèñÊï∞ÊçÆ
setDataSource(DataSourceType.LOCAL)

Vue.prototype.$axios = axios;
window.axios = axios;
Vue.use(ElementUI);
Vue.prototype._ = _;
window._ = _;

Vue.config.productionTip = false

// Ê∑ªÂä†ÂÖ®Â±ÄÈîôËØØÂ§ÑÁêÜÂô®Êù•Â§ÑÁêÜResizeObserverÂæ™ÁéØÈîôËØØ
window.addEventListener('error', (event) => {
  // Ê£ÄÊü•ÊòØÂê¶ÊòØResizeObserverÂæ™ÁéØÈîôËØØ
  if (event.message && event.message.includes('ResizeObserver loop completed with undelivered notifications')) {
    console.warn('ÊçïËé∑Âà∞ResizeObserverÂæ™ÁéØÈîôËØØÔºåÂ∑≤ÂøΩÁï•:', event.message);
    event.preventDefault(); // ÈòªÊ≠¢ÈîôËØØÊòæÁ§∫Âú®ÊéßÂà∂Âè∞
    return false;
  }
});

// Â§ÑÁêÜÊú™ÊçïËé∑ÁöÑPromiseÈîôËØØ
window.addEventListener('unhandledrejection', (event) => {
  if (event.reason && event.reason.message && 
      event.reason.message.includes('ResizeObserver loop')) {
    console.warn('ÊçïËé∑Âà∞ResizeObserver PromiseÈîôËØØÔºåÂ∑≤ÂøΩÁï•:', event.reason.message);
    event.preventDefault();
    return false;
  }
});

// üîß Á°Æ‰øùÂú®Â∫îÁî®ÂêØÂä®ÂâçÂàùÂßãÂåñÊï∞ÊçÆÈõÜÈÖçÁΩÆ
async function initializeApp() {
  try {
    // üîç ËÆ∞ÂΩïÂ∫îÁî®ÂêØÂä®ÂâçÁöÑÂÜÖÂ≠ò‰ΩøÁî®ÊÉÖÂÜµ
    logMemoryUsage('Â∫îÁî®ÂêØÂä®Ââç');
    
    console.log('ÂºÄÂßãÂàùÂßãÂåñÊï∞ÊçÆÈõÜÈÖçÁΩÆ...');
    await initializeDatasetConfig();
    console.log('ÂÖ®Â±ÄÊï∞ÊçÆÈõÜÈÖçÁΩÆÂàùÂßãÂåñÂÆåÊàê');
    
    // üîç ËÆ∞ÂΩïÈÖçÁΩÆÂä†ËΩΩÂêéÁöÑÂÜÖÂ≠ò‰ΩøÁî®ÊÉÖÂÜµ
    logMemoryUsage('ÈÖçÁΩÆÂä†ËΩΩÂêé');
    
    // Âú®ÂºÄÂèëÁéØÂ¢É‰∏ãÂêØÂä®ÂÜÖÂ≠òÁõëÊéßÂíåËØ∑Ê±ÇÊµãËØïÂ∑•ÂÖ∑
    if (process.env.NODE_ENV === 'development') {
      console.log('üöÄ ÂºÄÂèëÁéØÂ¢ÉÔºöÂêØÂä®ÂÜÖÂ≠òÁõëÊéß');
      globalMemoryMonitor.start(10000); // ÊØè10ÁßíÁõëÊéß‰∏ÄÊ¨°
      
      // ÂêØÁî®Êï∞ÊçÆÈõÜÂàáÊç¢ÊµãËØïÂ∑•ÂÖ∑
      try {
        const testModule = await import('@/utils/datasetSwitchTest');
        console.log('üß™ Êï∞ÊçÆÈõÜÂàáÊç¢ÊµãËØïÂ∑•ÂÖ∑Â∑≤ÂêØÁî®');
        window.datasetSwitchTest = testModule.default;
      } catch (error) {
        console.warn('Êï∞ÊçÆÈõÜÂàáÊç¢ÊµãËØïÂ∑•ÂÖ∑Âä†ËΩΩÂ§±Ë¥•:', error);
      }
    }
    
    // ÈÖçÁΩÆÂàùÂßãÂåñÂÆåÊàêÂêéÂêØÂä®VueÂ∫îÁî®
    const app = new Vue({
      router,
      store,
      vuetify,
      render: h => h(App)
    }).$mount('#app');
    
    // üîç ËÆ∞ÂΩïVueÂ∫îÁî®ÂêØÂä®ÂêéÁöÑÂÜÖÂ≠ò‰ΩøÁî®ÊÉÖÂÜµ
    logMemoryUsage('VueÂ∫îÁî®ÂêØÂä®Âêé');
    
    // Âú®window‰∏äÊö¥Èú≤VueÂÆû‰æãÔºà‰ªÖÂºÄÂèëÁéØÂ¢ÉÔºâ
    if (process.env.NODE_ENV === 'development') {
      window.vueApp = app;
      console.log('üîß VueÂ∫îÁî®ÂÆû‰æãÂ∑≤ÊåÇËΩΩÂà∞ window.vueApp');
    }
    
  } catch (error) {
    console.error('ÂÖ®Â±ÄÊï∞ÊçÆÈõÜÈÖçÁΩÆÂàùÂßãÂåñÂ§±Ë¥•:', error);
    
    // Âç≥‰ΩøÈÖçÁΩÆÂàùÂßãÂåñÂ§±Ë¥•Ôºå‰πüÂêØÂä®Â∫îÁî®Ôºà‰ΩøÁî®ÂõûÈÄÄÈÖçÁΩÆÔºâ
    console.warn('‰ΩøÁî®ÂõûÈÄÄÈÖçÁΩÆÂêØÂä®Â∫îÁî®...');
    new Vue({
      router,
      store,
      vuetify,
      render: h => h(App)
    }).$mount('#app');
  }
}

// ÂêØÂä®Â∫îÁî®
initializeApp();
