import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store' // Áõ¥Êé•ÂØºÂÖ•store

Vue.use(VueRouter)

// Ë∑ØÁî±ÊáíÂä†ËΩΩ
const HomeView = () => import(/* webpackChunkName: "home" */ '../views/HomeView.vue')

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: './',
  routes,
  // ÊªöÂä®Ë°å‰∏∫
  scrollBehavior() {
    return { x: 0, y: 0 }
  }
})

// ÂÖ®Â±ÄË∑ØÁî±ÂÆàÂç´ - Áî®‰∫éÈ°µÈù¢Âä†ËΩΩËøõÂ∫¶Êù°
router.beforeEach((to, from, next) => {
  // Áõ¥Êé•‰ΩøÁî®ÂØºÂÖ•ÁöÑstoreÂØπË±°ÔºåËÄå‰∏çÊòØVue.prototype.$store
  store.dispatch('setLoading', true);
  next();
});

router.afterEach(() => {
  // Âª∂ËøüÂÖ≥Èó≠Âä†ËΩΩÁä∂ÊÄÅÔºåÊèêÂçáÁî®Êà∑‰ΩìÈ™å
  setTimeout(() => {
    store.dispatch('setLoading', false);
  }, 200);
});

export default router
