/**
 * APIËØ∑Ê±ÇÁºìÂ≠òÊúçÂä°
 */

/**
 * ÂàõÂª∫ËØ∑Ê±ÇÁºìÂ≠ò
 */
const apiCache = {
  cache: new Map(),
  
  /**
   * ÁºìÂ≠òAPIËØ∑Ê±ÇÁªìÊûú
   * @param {string} key - ÁºìÂ≠òÈîÆÂêç
   * @param {Promise<any>} promise - APIËØ∑Ê±ÇPromise
   * @param {number} [ttl=60000] - ÁºìÂ≠òÊúâÊïàÊúüÔºàÊØ´ÁßíÔºâ
   * @returns {Promise<any>} - APIËØ∑Ê±ÇÁªìÊûú
   */
  cacheRequest(key, promise, ttl = 60000) {
    if (!this.cache.has(key)) {
      // ÁºìÂ≠òPromiseÁªìÊûú
      const cacheEntry = {
        promise,
        timestamp: Date.now(),
        ttl
      };
      
      this.cache.set(key, cacheEntry);
      
      // Â§ÑÁêÜpromiseÂÆåÊàêÂêéÁöÑÊìç‰Ωú
      promise.catch(() => {
        // ËØ∑Ê±ÇÂ§±Ë¥•Âêé‰ªéÁºìÂ≠ò‰∏≠ÁßªÈô§
        this.cache.delete(key);
      });
    } else {
      const cacheEntry = this.cache.get(key);
      
      // Ê£ÄÊü•ÁºìÂ≠òÊòØÂê¶ËøáÊúü
      if (Date.now() - cacheEntry.timestamp > cacheEntry.ttl) {
        // ÁºìÂ≠òËøáÊúüÔºåÈáçÊñ∞ËØ∑Ê±Ç
        cacheEntry.promise = promise;
        cacheEntry.timestamp = Date.now();
      }
      
      return cacheEntry.promise;
    }
    
    return promise;
  },
  
  /**
   * Ê∏ÖÈô§ÊåáÂÆöÁºìÂ≠ò
   * @param {string} key - ÁºìÂ≠òÈîÆÂêç
   */
  invalidate(key) {
    this.cache.delete(key);
  },
  
  /**
   * Ê∏ÖÈô§ÊâÄÊúâÁºìÂ≠ò
   */
  invalidateAll() {
    this.cache.clear();
  }
};

export default apiCache; 